.video-component-container {
    /* background: blue; */
    height: 100%;
    width: 100%;
    margin: auto;
    display: block;
    padding-top: 5vh;
    overflow: hidden;
}

.performances-component-container{
    height: 90%;
    width: 70%;
    margin: auto;
    display: block;
    margin-top: 10vh;
}

#performances-component {
    display: grid;
    height: 100%;
    width: 100%;
    grid-gap: 20px;
    /* grid-template-columns: repeat(9, 20vmin);
    grid-template-rows: repeat(10, 20vmin); */
    grid-template-columns: repeat(9, 1fr);
    grid-auto-rows: minmax(1, auto);
    
    grid-template-areas: 
    "con con con con con con con con con"
    "con con con con con con con con con"
    "con con con con con con con con con"
    "con con con con con con con con con" 
    "con con con con con con con con con"
    "con con con con con con con con con"
    "con con con con con con con con con";
}

